<template>
  <div class="comments-page-container">
    <div class="comment-type-block" v-if="result[t].length > 0" v-for="t in type" :key="`list-${t}`">
      <div class="comment-type-title">{{t.toUpperCase()}}</div>
      <div class="comments-list">
        <div class="comment-item" v-for="item in result[t]" :key="item.id">
          <img class="user-avatar" :src="`${item.user.avatarUrl}?param=50y50`">
          <div class="comment-content">
            <div class="user-name">
              <b>{{item.user.nickname}}</b>
              <span class="pl_20 comment-time">{{getTime(item.time)}}</span>
            </div>
            <div class="content">{{item.content}}</div>
            <div class="mt_10">
              <i :class="`iconfont icon-zan${item.liked ? '' : '-o'}`" />
              <span class="pl_10 ft_12">{{numberHandle(item.likedCount)}}</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import { mapGetters } from 'vuex';
  import request from '../assets/utils/request';
  import timer from '../assets/utils/timer';
  export default {
    name: "Comment",
    data() {
      return {
        offset: 0,
        limit: 20,
        total: 0,
        result: {
          hot: [],
          latest: [],
        },
        queryId: '',
        loading: false,
        type: ['hot', 'latest'],
      }
    },
    computed: {
      ...mapGetters({
        playNow: 'getPlaying',
      })
    },
    watch: {
      playNow({ id }) {
        this.resetData();
        if (id !== this.queryId) {
          this.getComments(id);
        }
      }
    },
    created() {
      if (this.playNow && this.playNow.id) {
        this.getComments(this.playNow.id);
      }
    },
    methods: {
      getComments(id, more) {
        const { offset, limit, total, loading } = this;
        if ((((offset + limit) > total) && more) || loading) {
          return;
        }
        this.loading = true;
        request({
          api: 'MUSIC_COMMENTS',
          data: { offset, limit, total, id }
        }).then((res) => {
          if (id !== this.playNow.id) {
            return;
          }
          this.offset += limit;
          this.total = res.total;
          this.queryId = id;
          this.loading = false;
          this.result = {
            hot: [ ...this.result.hot, ...res.hotComments ],
            latest: [ ...this.result.latest, ...res.comments ],
          };
        })
      },
      resetData() {
        this.offset = 0;
        this.limit = 20;
        this.total = 0;
        this.result = {
          hot: [],
          latest: [],
        };
        this.queryId = '';
        this.loading = false;
      },
      getTime(t) {
        return timer(t).str('YY-MM-DD HH:mm:ss');
      },
      numberHandle(n) {
        return n > 1000 ? `${Number(n / 1000).toFixed(1)}k` : n
      }
    }
  }
</script>

